
import {mapMutations} from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Cell from 'vux/src/components/cell/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XSwitch from 'vux/src/components/x-switch/index.vue'

export default {
  name: 'hello',
  data () {
    return {
      tab: ['省', '市', '区'],
      selected: '请选择',
      show: false,
      index: 0,
      active: 0,
      areaList: [],
      cityList: [],
      distList: []
    }
  },
  props: {
    title: {
      type: String,
      default: ''
    }
  },
  directives: {
    TransferDom
  },
  components: {
    Group,
    Popup,
    Cell,
    Tab,
    TabItem,
    XButton,
    Swiper,
    SwiperItem,
    XSwitch
  },
  methods: {
    // 获取一级列表
    getAreaList (id) {
      this.$http({
        method: 'jsonp',
        url: 'http://liuwbox.com/zzbao/app/insurance/area.htm?id=' + id,
        jsonp: 'callback',
        jsonpCallback: 'json'
      })
      .then(res => {
        this.areaList = res.body.data.areaList
      })
      this.getArea(id)
    },
    // 获取二级列表
    findCity (id, text) {
      this.index = this.active = 1
      this.$http({
        method: 'jsonp',
        url: 'http://liuwbox.com/zzbao/app/insurance/area.htm?id=' + id,
        jsonp: 'callback',
        jsonpCallback: 'json'
      })
      .then(res => {
        this.cityList = res.body.data.areaList
      })
      this.tab[0] = text
      this.getArea(id)
    },
    // 获取三级列表
    findDist (id, text) {
      this.index = this.active = 2
      this.$http({
        method: 'jsonp',
        url: 'http://liuwbox.com/zzbao/app/insurance/area.htm?id=' + id,
        jsonp: 'callback',
        jsonpCallback: 'json'
      })
      .then(res => {
        this.distList = res.body.data.areaList
      })
      this.tab[1] = text
      this.getArea(id)
    },
    // 显示区域
    showDist (id, text) {
      this.tab[2] = text
      this.show = false
      this.selected = this.tab.join(' ')
      this.getArea(id)
    },
    // 提交数据
    submit () {
      this.show = false
      this.selected = this.tab.join(' ')
      this.getArea(this.selected)
      console.log(this.tab)
    },
    ...mapMutations({
      getArea: 'getInsuranceArea'
    })
  },
  created () {
    this.getAreaList(0)
  }
}



